import uniIcons from '../uni-icons/uni-icons.vue'
export default {
	name: 'UniIndexedList',
	components: {
		uniIcons
	},
	props: {
		options: {
			type: Array,
			default () {
				return []
			}
		},
		showSelect: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			lists: [],
			touchmove: false,
			touchmoveIndex: -1,
			itemHeight: 0,
			winHeight: 0,
			winHeightOrigin:0,
			scrollViewId: ''
		}
	},
	watch: {
		options: {
			handler: function() {
				this.setList()
			},
			deep: true
		}
	},
	created() {
		this.setList()
	},
	methods: {
		setList() {
			let winHeight = uni.getSystemInfoSync().windowHeight
			this.itemHeight = (winHeight-40) / this.options.length
			this.winHeightOrigin=winHeight
			this.winHeight = winHeight-40

			// if (!this.showSelect) {
			// 	this.lists = this.options;
			// 	return;
			// }
			// console.log(this.options)
			this.lists = this.options.map(value => {
				// console.log(value)
				let items = value.cities.map(item => {
					let obj = {}
					// for (let key in item) {
					obj['key'] = value.letter
					obj['city'] = item
					// }
					obj.checked = item.checked ? item.checked : false
					return obj
				})
				return {
					title: value.letter,
					key: value.letter,
					items: items
				}
			})
			// console.log(this.lists)
		},
		touchStart(e) {
			this.touchmove = true
			let pageY = e.touches[0].pageY
			let index = Math.floor(pageY / this.itemHeight)
			let item = this.lists[index]
			if (item) {
				this.scrollViewId = 'uni-indexed-list-' + item.key
				this.touchmoveIndex = index
			}
		},
		touchMove(e) {
			let pageY = e.touches[0].pageY
			let index = Math.floor(pageY / this.itemHeight)
			let item = this.lists[index]
			if (item) {
				this.scrollViewId = 'uni-indexed-list-' + item.key
				this.touchmoveIndex = index
			}
		},
		touchEnd() {
			this.touchmove = false
			this.touchmoveIndex = -1
		},
		onClick(idx, index) {
			let obj = {}
			for (let key in this.lists[idx].items[index]) {
				obj[key] = this.lists[idx].items[index][key]
			}
			let select = []
			if (this.showSelect) {
				this.lists[idx].items[index].checked = !this.lists[idx].items[index].checked
				this.lists.forEach((value, idx) => {
					value.items.forEach((item, index) => {
						if (item.checked) {
							let obj = {}
							for (let key in this.lists[idx].items[index]) {
								obj[key] = this.lists[idx].items[index][key]
							}
							select.push(obj)
						}
					})
				})
			}
			this.$emit('click', {
				item: obj,
				select: select
			})
		}
	}
}
